
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#img {
    width: 100%;
    height: 150px;
}
#box {
    width: 100%;
}
#input-box {
    margin-top: 5%;
    margin-left: 20%;
    margin-right: 20%;
}
input::-webkit-input-placeholder {
    text-align: center;
}
input[type='text'] {
    border-style: solid;
    border-radius: 18px;
    border-width: 1px;
    border-color: gray;
}
#i-phone {
    display: block;
    width: 100%;
    height: 35px;
}
#i-code {
    float: left;
    width: 50%;
    height: 25px;
    display: block;
    margin-top: 5%;
}
#s-send {
    float: right;
    width: 40%;
    margin-top: 5%;
    font-size: 12px;
    background-color: rgb(12, 153, 235);
}
#b-login {
    width: 60%;
    line-height: 30px;
    margin-top: 5%;
}
#error-label {
    width: 100%;
    display: block;
    color: red;
    margin-top: 10%;
    font-size: 18px;
}


#box {
    width: 100%;
}
#flex-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 5%;
}
#flex-item {
    width: 100%;
    height: 50px;
    line-height: 50px;
    background: green;
    color: white;
}
#flex-item[pos='middle']{
    margin-left: 10%;
    margin-right: 10%;
}
#content {
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 5%;
}
#content>p{
    text-align: left;
    font-size: 14px;
}
#tb-lesson {
    width: 100%;
    font-size: 12px;
}
#lesson-name {
    width: 60%;
}
#show-detail-box {
    width: 100%;
    height: 50px;
    margin-top: 5%;
    border: 1px dashed gray;
}
#s-detail {
    font-size: 12px;
    color: gray;
    height: 30px;
    line-height: 30px;
    display: block;
}
#s-detail-icon {
    display: block;
    font-size: 12px;
    color: gray;
}
#detail-box {
    margin-top: 10px;
}
#tb-detail {
    width: 100%;
    font-size: 12px;
}
#btn-task {
    margin-top: 5%;
    width: 100%;
}
